export const LAYOUT_INFO_SYMBOL = Symbol("layout info");
function layoutParseStringToNumber(info, value) {
    let res_value = -1;
    let visiable = true;
    if (info.endsWith("%")) { // 百分比布局
        const per = parseFloat(info);
        if (isFinite(per)) {
            res_value = value * per;
            visiable = res_value >= 0;
        }
    }
    else if (isFinite(info)) { // 明确的宽度值
        res_value = parseFloat(info);
        visiable = res_value > 0;
    }
    return {
        value: res_value,
        visiable
    };
}
/**
 * 一行一行地排
 * TODO...
 */
export class RowLayout {
    constructor(width = "auto", height = "auto", row_span = "2") {
        this.width = width;
        this.height = height;
        this.row_span = row_span;
        this.rows = [];
        this.items = new Set();
    }
    add(item) {
        if (!this.items.has(item)) {
            this.rows.push(item);
            this.items.add(item);
        }
        return true;
    }
    remove(item) { return false; }
    calcLauyout(width, height) {
        const res = new Map();
        return res;
    }
}
/**
 * 一列一列地排
 */
export class ColLayout {
    constructor(width = "auto", height = "auto", col_span = "2") {
        this.width = width;
        this.height = height;
        this.col_span = col_span;
        this.cols = [];
        this.items = new Set();
    }
    add(item) {
        if (!this.items.has(item)) {
            this.cols.push(item);
            this.items.add(item);
        }
        return true;
    }
    remove(item) {
        if (!this.items.has(item)) {
            const index = this.cols.indexOf(item);
            if (index !== -1) {
                this.cols.splice(index, 1);
            }
            this.items.delete(item);
        }
        return true;
    }
    calcLauyout(width, height) {
        const res = new Map();
        const auto_widthitems = [];
        let rest_width = width;
        // 根据可见元素数量显示相应数量的span空隙
        let visiable_item_num = this.cols.length;
        for (let item of this.cols) {
            const layout_info = item[LAYOUT_INFO_SYMBOL];
            const { value: item_width, visiable: item_visiable } = layoutParseStringToNumber(layout_info.width, width);
            const visiable = item_visiable && layout_info.visiable;
            layout_info.visiable = visiable;
            if (!visiable) {
                visiable_item_num -= 1;
                continue;
            }
            if (item_width !== -1) {
                rest_width -= item_width;
                res.set(layout_info.id, {
                    left: 0,
                    top: 0,
                    width: item_width,
                    height,
                    visiable
                });
            }
            else { // === 'auto' and other
                auto_widthitems.push(item);
            }
        }
        const col_span_info = layoutParseStringToNumber(this.col_span, width);
        const auto_item_width = Math.max(0, col_span_info.visiable
            ? ((rest_width - (visiable_item_num + 1) * col_span_info.value) / visiable_item_num)
            : (rest_width / visiable_item_num));
        let left_acc = col_span_info.value;
        // 设置宽度为auto的元素宽度
        for (let item of auto_widthitems) {
            const layout_info = item[LAYOUT_INFO_SYMBOL];
            res.set(layout_info.id, {
                left: 0,
                top: 0,
                width: auto_item_width,
                height,
                visiable: true
            });
        }
        // 设置left值
        for (let item of this.cols) {
            const layout_info = item[LAYOUT_INFO_SYMBOL];
            if (!layout_info.visiable) {
                continue;
            }
            const layout_bound = res.get(layout_info.id);
            if (layout_bound) {
                layout_bound.left = left_acc;
                left_acc += col_span_info.value + layout_bound.width;
            }
        }
        return res;
    }
}
/**
 * 网格Z型排
 * TODO...
 */
export class GridLayout {
    constructor(width = "auto", height = "auto", row_span = "2", col_span = "2") {
        this.width = width;
        this.height = height;
        this.row_span = row_span;
        this.col_span = col_span;
        /**
         * row -> col
         * [ t1 , t2 ]
         * [ t3 , t4 ]
         */
        this.grid = [[]];
        this.items = new Set();
        /**行数 */
        this.row_num = 1;
        /**列数 */
        this.col_num = 1;
        this._auto_extend_grid = false;
        this.buildGrid(this.row_num, this.col_num);
    }
    get max_size() {
        return this.row_num * this.col_num;
    }
    buildGrid(row_num = 2, col_num = 2) {
        const new_grid = Array.from({ length: row_num }, () => Array.from({ length: col_num }));
        for (let r = 0; r < row_num; r += 1) {
            new_grid[r] = Array.from({ length: col_num });
        }
        this.grid = new_grid;
        this.row_num = row_num;
        this.col_num = col_num;
        // 往新的表格里头填充
        for (let item of this.items) {
            if (!this.add(item)) {
                return;
            }
        }
    }
    autoExtendGrid(auto_extend_grid) {
        this._auto_extend_grid = auto_extend_grid;
        return this;
    }
    add(item) {
        if (this.items.has(item)) {
            return true;
        }
        if (this.items.size >= this.max_size) {
            // 容不下了
            if (this._auto_extend_grid) {
                if (this.row_num > this.col_num) {
                    this.buildGrid(this.row_num, this.col_num + 1);
                }
                else {
                    this.buildGrid(this.row_num + 1, this.col_num);
                }
                return this.add(item);
            }
            else {
                return false;
            }
        }
        this.items.add(item);
        // Z型填充
        return this.grid.some(row => {
            for (let i = 0; i < row.length; i += 1) {
                if (!row[i]) { // 空位置，进行填充
                    row[i] = item;
                    return true;
                }
            }
            return false;
        });
    }
    remove(item) { return false; }
    calcLauyout(width, height) {
        const res = new Map();
        return res;
    }
}
